import React, {useEffect, useState} from "react";
import * as axios from 'axios';

export const App = () => {
    const [data, setData] = useState('')

    useEffect(()=>{
        axios.get('https://alexeysparrow.github.io/photo-holiday/json/data.json')
            .then (res => {
                setData(res.data)
            })
    }, [])

    console.log(data)

    return (
        <div className='container'>

        </div>
    );
}

